@import '../../UI/styles/styles.scss';

.basketItem {
  display: grid;
  grid-template-columns: 20% 55% 15% 10%;
  font-weight: bold;
  align-items: center;
  padding: 30px;
  margin-top: 20px;
  font-size: 16px;
  transition: .3s;
  .basketImg {
    width: 120px;
    filter: $dropShadowIng;
  }
  button {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: $red;
    color: $white;
    outline: none;
    border: none;
    transition: .3s;
    cursor: pointer;
    font-size: 16px;
    &:hover {
      transform: scale(1.2);
      transition: .3s;
    }
  }
  &:hover {
    box-shadow: $boxShadowHover;
    transition: .3s;
  }
}
.checkOutButton {
  width: 500px;
  margin: 0 auto;
  text-align: center;
  button {
    @include bigBtn;
  }
}

@media (max-width: 530px) {
  .checkOutButton {
    width: 200px;
    button {
      width: 75px;
      height: 40px;
      font-size: 14px;
    }
  }
  .basketItem {
    grid-template-columns: 1fr 1fr 1fr;
    .basketImg {
      width: 100px;
    }
  }
  .basketItemTitle {
    display: none;
  }
}
@media (max-width: 420px) {
  .checkOutButton {
    button {
      margin-top: 30px;
    }
  }
}